<script>
// import * as sdk from "../../src/index"
import {OKXTonConnect, utils,UserRejectsError,
  OKX_TON_CONNECTION_COMPLETED,
  OKX_TON_CONNECTION_ERROR,
  OKX_TON_CONNECTION_RESTORING_COMPLETED,
  OKX_TON_CONNECTION_RESTORING_ERROR,
  OKX_TON_CONNECTION_RESTORING_STARTED,
  OKX_TON_CONNECTION_STARTED,
  OKX_TON_DISCONNECTION,
  OKX_TON_TRANSACTION_SENT_FOR_SIGNATURE,
  OKX_TON_TRANSACTION_SIGNED,
  OKX_TON_TRANSACTION_SIGNING_FAILED
} from "../../src/index"
  // from "okxconnectsdk";

var TON = new OKXTonConnect({metaData: {
            name:"dapp",
            icon:"",
            // url:window.location.href
            url:'okx.com'
        }})
window.ton = TON
// var testCM = new ConnectionManager()
// async function testconnect() {
//   await testCM.connect()
// }

import QrcodeVue from 'qrcode.vue'
import {TonProofService} from "@/ton/ton-proof-service";

export default {
  name: 'App',
  components: {
    QrcodeVue
  },
  data: ()=> {
    return {
      testTopic: '',
      testPublicKey: '',
      testProofPayload: '',
      pendingDeepLink: '-',
      logList: [],
      connectReady: false,
      connectStatusStr: '',
      disconnectStatusStr: '',
      localPubKey: '',
      localSecKey: '',
      testTransaction:'',
      testTransactionStr:'',
      walletAddress:'',
      testboc:'',
      ProofPayloadVerify:'',
      showModal: false,
      qrValue: 'https://www.okx.com/download',
      currentStatus:"",
      isChecked:localStorage.getItem("autoRefresh") == "true",
      handleChange:(isChecked) => {
        localStorage.setItem("autoRefresh",`${isChecked}`)
      }
    }
  },
  computed: {
    updateUniLink:function(){
      console.log(`update uni link: ${this.testTopic} ${this.testPublicKey}`)
      let pendingDeepLink = utils.generateDeepLink(
        {
          protocolVer: "1",
          channelId: this.testTopic,
          clientId: this.testPublicKey,
          dAppInfo: {
            name: "test",
            url: "-",
            iconUrl: "-"
          },
          requestId:"0",
          testPayload: this.testProofPayload
        }
      )
      return pendingDeepLink
    },
    getWalletAddress:function (){
      return this.walletAddress;
    },


  },
  methods: {
    async connectBtnClicked() {
        this.testTonSdk();

    },
    async disconnectAction() {
      try {
        console.log('get disconnect wallet btn clicked');
        await TON.disconnect();
      } catch (error) {
        console.log('Disconnect  error ==>> ',error.message);
        this.currentStatus = error.message;
      }
    },
    async resotreConnectAction() {
      console.log('get restore wallet btn clicked');
      TON.restoreConnection();
    },
    // clearCacheKeyPair: () => {
    //   console.log(`generate local key pair`)
    //   testCM.cleanCache()
    //   // testTonSdk()
    //
    // },
    async sendTransaction(){
      console.log('sendTransaction')
      console.log('sendTransaction------',this.testTransaction)
    if(this.testTransaction){
      // eslint-disable-next-line no-undef
      const sendData = JSON.parse(this.testTransaction)
      console.log('sendTransaction-------',sendData)
      TON.sendTransaction(sendData)
      return;
    }
      let transactionRequest =
          {"messages":[{"address":"EQARULUYsmJq1RiZ-YiH-IJLcAZUVkVff-KBPwEmmaQGH6aC","amount":"205000000","payload":"te6cckEBAgEAhwABbQ+KfqUAAADNgG7tIEATEtAIAO87mQKicbKgHIk4pSPP4k5xhHqutqYgAB7USnesDnCcECwbgQMBAJUlk4VhgBD3JEg1TUr75iTijBghOKm/sxNDXUBl7CD6WMut0Q85x4RafwA/Es89DBXoTxuqxVFxyBbzt9Rav2HBUKl7hmkvLuKHLBCW57aK"}],"validUntil":1792481054071,"network":-239,"from":"UQD8Szz0MFehPG6rFUXHIFvO31Fq_YcFQqXuGaS8u4ocsJdI"}

      try {
        var result = await TON.sendTransaction(transactionRequest)
        this.testboc = result.boc
      }catch (e) {
        if (e instanceof UserRejectsError){
          this.testboc = ""
        }else{
          this.testboc = ""
        }
      }
    },

    async testTonSdk(){
      if (TON.connected){
        console.log('you have connected wallet');
      }else {
        console.log('APP ---connect Proof--->>>>----',this.testProofPayload);
        let connectData = {
          tonProof:this.testProofPayload,
          redirect:"tg://resolve",
          openUniversalUrl:true
        };

        if (this.isPC()) {
          connectData.redirect = null;
          connectData.openUniversalUrl = false;
          console.log('Is PC, showing connectData',connectData);

          let url = await TON.connect(connectData);
          console.log('Is PC, showing modal ==>',url);
          if (url){
            this.qrValue = url;
            this.showModal = true;
          }

        }else {
          if(this.isSafariOrChrome()){
            connectData.redirect = null;
          }
          console.log('Is Mobile ==>',connectData);
          await  TON.connect(connectData);
        }
      }

    },

    closeModal() {
      this.showModal = false;
    },

    isPC() {
      const userAgentInfo = navigator.userAgent
      console.log('current device ua is ===>',userAgentInfo)

      const Agents = ["Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod"]
      let flag = true
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = false
          break
        }
      }
      return flag
    },
    isSafariOrChrome() {
      const userAgentInfo = navigator.userAgent
      const Agents = ["Safari", "Chrome"]
      let flag = false
      for (let v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = true
          break
        }
      }
      return flag
    },
  },
  created() {
    const events = [
      OKX_TON_CONNECTION_STARTED,
      OKX_TON_CONNECTION_COMPLETED,
      OKX_TON_CONNECTION_ERROR,
      OKX_TON_CONNECTION_RESTORING_STARTED,
      OKX_TON_CONNECTION_RESTORING_COMPLETED,
      OKX_TON_CONNECTION_RESTORING_ERROR,
      OKX_TON_DISCONNECTION,
      OKX_TON_TRANSACTION_SENT_FOR_SIGNATURE,
      OKX_TON_TRANSACTION_SIGNED,
      OKX_TON_TRANSACTION_SIGNING_FAILED,
    ]

    events.forEach(eventName => {
      window.addEventListener(eventName, (event) => {
        console.log(eventName,event.detail)
        this.currentStatus = eventName
      });
    });
  },

  mounted() {
    this.connectReady = true
    // console.log('test connect called')
    // testconnect()
    // const keyPairInfo = testCM.getCryptoInfo()
    // console.log(keyPairInfo)
    // this.localPubKey = keyPairInfo.kp.publicKey
    // this.localSecKey = keyPairInfo.kp.secretKey
    // this.testPublicKey = this.localPubKey
    // this.testTopic = keyPairInfo.topic?.content

    TON.onStatusChange((walletInfo) => {
      console.log('APP ---TON.onStatusChange>>----', walletInfo);
          if (walletInfo) {
            this.walletAddress = walletInfo.account.address;
            if (this.testProofPayload) {
              var checkProofData = {
                address: this.walletAddress,
                network: walletInfo.account.chain,
                public_key:walletInfo.account.publicKey,
                proof:{
                  ... walletInfo.connectItems.tonProof.proof,
                  state_init: walletInfo.account.walletStateInit
                }
              }
              console.log(`checkProof => ${JSON.stringify(checkProofData)}`)
              new TonProofService().checkProof(checkProofData).then((result) => {
                this.ProofPayloadVerify = `${result}`;
              }).catch((error)=>{
                this.ProofPayloadVerify = `error ${error}`;
              })
            } else {
              this.ProofPayloadVerify = ": no proof";
            }
            this.showModal = false;

          } else {
            this.walletAddress = "disconnected";
          }
        }, (err) => {
          this.showModal = false;
          if (err instanceof UserRejectsError){
            this.walletAddress = ``;
          } else{
            this.walletAddress = `  ${err.message}`;
          }
        }
    )
    if (this.isChecked){
      TON.restoreConnection()
    }
    //
    // TON.onStatusChange(function (walletInfo) {
    //
    //
    // }.bind(this));

  }
}
</script>